import {Cell, Input, Item, Table, Header, GTitle} from '../../sass/table.styles';
import {Formik} from "formik";
import React, {useState, useEffect} from 'react';
import styled from 'styled-components';
import API from "../../API";
import {Image,Button} from 'antd';
import {UploadOutlined} from '@ant-design/icons'
import {ImageContainer, Images, ImageTitle, UploadBlock} from "./pensioners_info.styles";
import {FileButton} from "../others/elements/elements";
import {capitalize} from "../../tools";
import {useRouteMatch} from 'react-router-dom'


const default_user_image = 'https://lh3.googleusercontent.com/proxy/6N7eJAtkRdX9OCB_5bLnqxyf4uddZSZg-K8fEszf' +
    'zDgjsstVJJekp-ONYnUjiPdZCbL7sSsEtF-gtEXY4quLhUih2f7IBKlLlmE_MY4QwdA'

const Pensioners_info = () => {
    const user_id = 6
    const match = useRouteMatch();
    const mode = match.params.mode
    const [initialValues, setInitValues] = useState(null)
    const [images, setImages] = useState({})
    useEffect(async () => {
        const response = await API.getTable(`/pensioner/agent_info/${user_id}`, null, 'get')
        setInitValues(response?.data)
        const images = await API.request('/pensioner/get_images', {id_pensioner: user_id}, 'put')
        setImages(images?.data.data[0])
    }, [])
    if (!initialValues) return null
    const getImage = key => {
        let item = images?.[key]
        const title = capitalize(key.replaceAll('_',' ').replaceAll('image',''))
        if (Object.keys(images).length === 0) return null
        return <>
            <ImageTitle>{title}</ImageTitle>
            <Image
                width={800}
                src={item ?? default_user_image}
            />
            <UploadBlock>
                <FileButton callback={updateImage(key)} />
            </UploadBlock>

        </>
    }
    console.log(match)
    console.log(images)
    const updateImage = key => {
        return async e => {
            if (e.target.files.length === 0) return
            const file = e.target.files[0]
            const formData = new FormData();
            formData.append('image', file)
            let response = await API.request(`/pensioner/upload/${key}`, formData, 'put')
            const filename = response?.data.data
            response = await API.request('/pensioner/update_images', {mode: key, filename, id: user_id}, 'put')
            const url = response?.data.data
            setImages({...images, [key]: url})
        }
    }
    return <>
        <GTitle>תמונות</GTitle>
        <Images>
            {getImage(mode)}
        </Images>
        <GTitle>לבנות השדות האלו</GTitle>
        <Formik initialValues={initialValues} onSubmit={async data => {
            await API.request('/pensioner/agent_info', {data, id: user_id}, 'put')
        }}>
            {formik => <><Table>

                {createItem('דוא"ל ', 'agent_name', formik, 'white')}
                {createItem('טלפון ', 'agent_telephone', formik, 'white')}
                {createItem('שם  סוכן', 'agent_mail', formik, 'white')}
            </Table>
                <Table>
                    {createItem('הערות ', 'remarks', formik, '#d1d1d1')}
                </Table>

            </>}
        </Formik>

    </>
}
const createItem = (title, value, options, color) => {
    const {handleChange, handleSubmit, values, errors} = options
    return <Item>
        <Header color={color}>{title}</Header>
        <Cell><Input onChange={handleChange} value={values[value] || ''}
                     name={value} error={errors[value]} onBlur={handleSubmit}/></Cell>
    </Item>
}



export default Pensioners_info