import styled from 'styled-components';


export const Dash = styled.div`
  display: flex;
  width: 90%;
  margin: 0 auto;
  flex-wrap: wrap;
`


export const Header = styled.div`
  margin: 0 auto;
  margin-top: 20px;
  width: 60%;
`
export const DashMain = styled.div`
  width: 371px;
  min-height: 393px;
  margin-top: 50px;
  margin-right: 80px;
  background: #F3FEFE;
`
export const DashInput = styled.input`
  width: 100%;
  height: 40px;
  border: 1px solid #c4c4c4;
  outline: none;
  font-size: 23px;
  background: #F3FEFE;
`
export const DashImage = styled.img`
  width: 100%;
  height: 172px;
`
export const DashText = styled.h3`
  font-size: 24px;
  color : #9A9A9A
`