import {Menu, Dropdown, Button, Modal, Input} from 'antd';
import {DownOutlined} from '@ant-design/icons';
import styled from 'styled-components'
import dash_image from './../../source/images/dash.jpg'
import {Formik, useFormik} from 'formik'
import React, {useEffect, useState} from 'react'
import {addTable, deleteTable, formatDate, getTable, tableFilter, toObject, updateTable} from "../../tools";
import {Dash, DashImage, DashInput, DashMain, DashText, Header} from "./dashboard.styles";
import {forms_data, header_data} from "./dashboard.data";
import Text from "antd/es/typography/Text";
import Title from "antd/es/typography/Title";
import {Preloader} from "../risk_insurances/risk_insurances_table";
import {useSelector} from "react-redux";
import {NavLink} from "react-router-dom";

// Медицинское стразование
// Ипотечные суды
export const TopSideBar = () => {
    const elements = header_data.map(item =>
        <Dropdown overlay={<Menu>{item.map(itemq =>
            <NavLink to={`${process.env.PUBLIC_URL}/dashboard`}><Menu.Item>{itemq}</Menu.Item></NavLink>

        )}</Menu>}>
            <a className="ant-dropdown-link" onClick={e => e.preventDefault()}>
                <Button>{item[0]} <DownOutlined/></Button></a></Dropdown>
    )
    return  <Header>{elements}</Header>

}

const DashBoard = () => {

    const [tables, setData] = useState({loading : true})
    const [form, showForm] = useState(null)
    const createInput = (value, formik) => {
        const {handleChange, handleSubmit, values, errors} = formik
        return <DashInput onChange={handleChange} value={values[value]} name={value} onBlur={handleSubmit}/>
    }
    const user_id = useSelector(state => state.auth.id)
    const mount = async () => {
        const savF = tableFilter(['half_decade_deposit', 'total_monthly_deposit']),
            loansF = tableFilter(['salary_month', 'yeats_to_end']),
            mortF = tableFilter(['years_to_end', 'payment_months']),
            wiiF = tableFilter(['monthly_compensation_amount']),
            insur_livesF = tableFilter(['monthly_compensation_amount', 'lump_sum']),
            insur_pensF = tableFilter(['exp_rewards_center', 'exp_compensation_component',
                'exp_total', 'expected_sum']),
            savings = await getTable('/deposit_savings/getsavings', user_id, 'post'),
            insur_doctors = await getTable('/insur_doctors/get', user_id, 'post'),
            edu = await getTable('/edu_funds/get', user_id, 'post'),
            loans = await getTable(`/loans/get_info/${user_id}`),
            mortgage = await getTable(`/mortgages/get_info/${user_id}`),
            wii = await getTable(`/work_incapacity_insurances/get_info/${user_id}`),
            insur_lives = await getTable(`/insur_lives/get_info/${user_id}`),
            insur_pens = await getTable(`/insur_pensioners/get_info/${user_id}`)
        setData({
            savings: savF(savings), edu, insur_doctors,
            loans: loansF(loans), mortgage: mortF(mortgage), wii: wiiF(wii),
            insur_lives: insur_livesF(insur_lives), insur_pens: insur_pensF(insur_pens)
        })
    }
    useEffect(mount, [])
    const transpiler = (name, user_id, method) => {
    const {values, titles, path} = forms_data[name]
    const initValues = tables[name]
    if (!initValues) return null
    const elements = initValues.map(item => <Formik initialValues={item}
                                                    onSubmit={data => updateTable(`${path}/update`, data, user_id, method)}>
        {formik =>
            <DashMain>
                <DashText>{name.replace('_', ' ').toUpperCase()}</DashText>
                {createInput('title', formik)}
                <DashImage src={dash_image}/>
                {values.map((item, i) => (
                    <>
                        <DashText>{titles[i]}</DashText>
                        {createInput(item, formik)}
                    </>
                ))}
                <button onClick={() => {
                    deleteTable(`${path}/delete/${item.id}`)
                    setData({...tables, [name]: initValues.filter(i => i.id !== item.id)})
                }}>delete
                </button>
            </DashMain>}
    </Formik>)
    return <>{elements}</>
    }
    if (tables.loading) return <Preloader />
    return <>
        <Dash>
            {transpiler('insur_doctors', user_id)}
            {transpiler('savings', user_id)}
            {transpiler('edu', user_id)}
            {transpiler('loans', null, 'put')}
            {transpiler('mortgage', null, 'put')}
            {transpiler('wii', null, 'put')}
            {transpiler('insur_lives', null, 'put')}
            {transpiler('insur_pens', null, 'put')}
        </Dash>
        {Object.entries(forms_data).map(item => item[0]).map(item => (
            <Button onClick={() => showForm(item)}>{item}</Button>
        ))}
        {form && <DashForm items={forms_data[form]}
                           submit={data => {
                               addTable(`${forms_data[form].path}/new`, {...data, id_pensioner: user_id})
                               setData({...tables, [form]: [...tables[form], data]})
                               showForm(null)
                           }} close={() => showForm(null)}/>}
    </>
}
const DashForm = ({submit: onSubmit, items,close}) => {
    const {values, titles} = items
    const createInput = (value, formik) => {
        const {handleChange, values} = formik
        return <Input onChange={handleChange} value={values[value]} name={value}/>
    }
    const formik = useFormik({initialValues: toObject(values), onSubmit});
    return <Modal title="Basic Modal" visible={true} onOk={formik.handleSubmit} onCancel={close}>
        <Title level={4}>כותרת</Title>
        {createInput('title', formik)}
        {values.map((item, index) => (
            <>
                <Title level={4}>{titles[index]}</Title>
                {createInput(item, formik)}
            </>
        ))}
    </Modal>
}

export default DashBoard
