import React from "react";
import API from "./API";

export const formatDate = (date = new Date()) => {
    let dd = date.getDate();
    if (dd < 10) dd = '0' + dd;
    let mm = date.getMonth() + 1;
    if (mm < 10) mm = '0' + mm;
    let yy = date.getFullYear() % 100;
    if (yy < 10) yy = '0' + yy;
    return '20' + yy + '-' + mm + '-' + dd;
}

export const ObjectProzentsToInt = (data) => {
    const e = (item) => {
        if (item === null) {
            return 0
        }
        if (typeof item == 'number') {
            return item
        }
        return Number(item.replace('%', ''))
    }
    return Object.fromEntries(Object.entries(data).map(item => [item[0], e(item[1])]))
}
export const ObjectProzentsToIntReverse = (data) => {
    const e = item => {
        if (item[1]===null){
            return 0
        }
        return item[1]
    }
    return Object.fromEntries(Object.entries(data).map(item => [item[0], e(item)]))
}
export const getTable = async (path,user_id=null,method='get') => {
    const response = await API.getTable(path,user_id,method);
    return response.data.data
}
export const updateTable = async (path,data,user_id=null,method='post') => {
    await API.updateTable(path,data,user_id,method)
}
export const tableFilter = (args,rez=0) => res => {
    const data = {};
    args.forEach(item => data[item] = res[item] ?? rez)
    // return {...res,...data}
    return res.map(item => ({...item,...data}))
}
export const deleteTable = async path => {
    await API.deleteTable(path)
}
export const toObject = items => {
    let data = {};
    items.forEach(item => data[item] = '')
    return data
}
export const addTable = async (path,data) => {
    await API.addTable(path,data)
}
export const capitalize = string => {
    return string.charAt(0).toUpperCase() + string.slice(1);
}

export const isNull = (value) => value === null || value === undefined