import {Cell, GTitle, Header, Input, Item, Link, Table, Title} from "../../sass/table.styles";
import {NavLink} from "react-router-dom";
import {Formik} from "formik";
import {pension_funds_validator} from "../../validator";
import {SaveButton} from "../risk_insurances/risk_insurances_table";
import {useDispatch, useSelector} from "react-redux";
import {logout} from "../../reducers/auth_reducer";


const initialValues = {

}



const PensionerData = () => {
    // נתוני לקוח
    const dispatch = useDispatch()
    const {username} = useSelector(state => state.auth)
    const createItem = (title) => <Item>
        <Header color={'#e38c12'}>{title}</Header>
        <Cell><Input /></Cell>
    </Item>
    return <div>
        <Title color={'#3d484d'}>נתוני לקוח </Title>
        <Link onClick={() => dispatch(logout(username))}>Logout</Link>
        <Link as={NavLink}  to={`${process.env.PUBLIC_URL}/mainpage`} px={5}>Back</Link>
                <Table>
                    {createItem('מספר תיק  בן בת זוג')}
                </Table>
                <Table>
                    {createItem('מספר פנימי שלנו ')}
                    {createItem('מספר תיק ')}
                </Table>
                <Table>
                    {createItem('פרטי בת/בן זוג')}
                </Table>
                <Table>
                    {createItem('פרטים שלי')}
                </Table>
                <Table>
                    {createItem('סטטוס תעסוקה')}
                    {createItem('עצמאי/שכיר/וגם וגם')}
                </Table>
                <Table>
                    {createItem('פרטי קשר')}
                </Table>
        <GTitle>ריך לתת אפשרות להוסיף שדה ילדים אם יש לדוגמה 8 </GTitle>
                <Table>
                    {createItem('פרטי ילדים ')}
                    {createItem('שדה תוצאה')}
                    {createItem('תאריכים')}
                </Table>
                <GTitle>צריך אפשרות להוסיף ולהוריד שורות </GTitle>
                <Table>
                    {createItem('פירוט תוכניות קיימות ')}
                    {createItem('מספר ')}
                    {createItem('פירוט תוכניות קיימות ')}
                </Table>
                <SaveButton />
    </div>
}


export default PensionerData