import {Menu, Button} from 'antd';
import {useState} from 'react'
import {CloseOutlined, MenuOutlined} from '@ant-design/icons'
import styled, {keyframes} from 'styled-components'
import {NavLink} from "react-router-dom";
import {Images} from "../../pensioners_info/pensioners_info.styles";

const {SubMenu} = Menu;
const Sidebar = function () {
    const [collapse, toggleCollapse] = useState(null)
    const [open, toggleOpen] = useState(false)
    // const sidebarIcon = !collapse ? <CloseOutlined
    //         onClick={() => toggleCollapse(!collapse)} style={{color : 'red',fontSize : '25px',
    //     top: '50px',left: '25px',position: 'absolute'}}/> :
    const sidebarIcon =
        // <PlusOutlined onClick={() => toggleCollapse(closing)}/>
        <CloseOutlined
            onClick={() => toggleCollapse(closing)} style={{
            color: 'red', fontSize: '25px',
            top: '50px', left: '25px', position: 'absolute'
        }}/>

    return (
        <>
            <Container animation={collapse}>
                <SidebarStyle>
                    {sidebarIcon}
                    {/*pension*/}
                    <Item key="0" onClick={() => toggleOpen(!open)} border={true}>פנסיה</Item>
                    {open &&
                    <><NavLink to={`${process.env.PUBLIC_URL}/pensioner_info/image_education_funds`}><Item key="1">פנסיה</Item></NavLink>
                    {/*Education fund*/}
                    <NavLink to={`${process.env.PUBLIC_URL}/pensioner_info/
                    image_insur_doctors`}><Item key="2">קרן השתלמות</Item></NavLink>
                    {/*Provident funds*/}
                    <NavLink to={`${process.env.PUBLIC_URL}/pensioner_info/
                    image_insur_lives`}><Item key="3">קופות גמל</Item></NavLink>
                    {/*Executive Gamble Insurance Funds...*/}
                    <NavLink to={`${process.env.PUBLIC_URL}/pensioner_info/
                    image_insur_pensioners`}><Item key="4">ביטוח
                        מנהלים </Item></NavLink>
                    {/*Risk insurance*/}
                    <NavLink to={`${process.env.PUBLIC_URL}/pensioner_info/
                    image_loans`}><Item key="5">ביטוח ריסק</Item></NavLink>
                    <NavLink to={`${process.env.PUBLIC_URL}/pensioner_info/
                    image_monthly_deposit_savings`}><Item key="6">ביטוח אובדן כושר עבודה</Item></NavLink>
                    <NavLink to={`${process.env.PUBLIC_URL}/pensioner_info/
                    image_mortgages`}><Item key="7">פרישה </Item></NavLink>
                    <NavLink to={`${process.env.PUBLIC_URL}/pensioner_info/
                    image_work_incapacity_insurances`}> <Item key="8">הגנה כלכלית
                        אישית </Item></NavLink></>
                    }
                    <Item key="9" border={true}>הגנה כללית למשפחה </Item>
                    <Item key="10">יעד חסכון</Item>
                    <Item key="11">ניהל יעד תשואה</Item>
                    <Item key="12">עלויות דמי ניהול ומעקב </Item>
                    <Item key="13">עלויות ביטוח</Item>
                    <Item key="14">ניהול הלוואות</Item>
                </SidebarStyle>
            </Container>
            <MenuOutlined style={{
                cursor: 'pointer', fontSize: '40px', color: 'black',
                position: 'absolute', right: '20px', top: '20px'
            }} onClick={() => toggleCollapse(opening)}/>
        </>
    );
}

const SideItem = ({key,border=false,route,title,callback=()=>{}}) => {
    if (route) return <NavLink to={`${process.env.PUBLIC_URL}/pensioner_info/
                    ${route}`}><Item key={key} border={border}>{title}</Item></NavLink>
    return <Item key={key} border={border} onClick={callback}>{title}</Item>
}


const Container = styled.div`
  //width: 250px;
  position: fixed;
  z-index: 2;
  right: 0;
  top: 50%;
  transform: translate(0, -50%);
  height: 90%;
  width: 300px;
  animation-name: ${props => props.animation ?? 'none'};
  animation-duration: 1s;
  animation-fill-mode: forwards;
`
const SidebarStyle = styled.div`
  background: #313EB0;
  border-radius: 20px;
  width: 100%;
  height: 100%;
  border: 2px solid #C4c4c4;
`
const Item = styled.h2`
  font-size: 23px;
  color: #FFF;
  text-align: center;
  margin-top: 30px;
  cursor: pointer;
  border: ${props => props.border ? '2px solid yellow' : null};
`
const closing = keyframes`
  from {
    right: 0;
  }

  to {
    right: -50%;
  }
`
const opening = keyframes`
  from {
    right: -50%;
  }

  to {
    right: 0;
  }

`

export default Sidebar