import {Button} from "antd";
import {UploadOutlined} from "@ant-design/icons";
import {BackgroundBlock, TypeFile} from "./elements.styles";

export const FileButton = ({callback}) => {
    return <BackgroundBlock>
        <Button style={{
            height: '100%', width: '90%', margin: '0 auto', display: 'block', fontSize: 17
        }}>
            <UploadOutlined/>  Upload
        </Button>
        <TypeFile type={'file'} top={0} onChange={callback}/>
    </BackgroundBlock>
}