import {Cell, Input, Item, Table, Header, Title, Link, GTitle} from '../../sass/table.styles';
import { Formik} from "formik";
import {useDispatch, useSelector} from "react-redux";
import {getTableData, logout, saveTable} from "../../reducers/auth_reducer";
import {formatDate} from "../../tools";
import React, {useState, useEffect} from 'react';
import {tableValidator} from "../../validator";
import {Skeleton,Card} from "antd";
import {NavLink, Redirect} from 'react-router-dom';
import {Button} from 'antd';
import styled from 'styled-components';

const Risk_insurances_table = () => {
    const {id: id_pensioner,username} = useSelector((state) => state.auth);
    const [initialValues, setInitValues] = useState(null)
    const [toogle, setToggle] = useState(false)
    const dispatch = useDispatch()
    useEffect(async () => {
        const {payload : response} = await dispatch(getTableData(id_pensioner))
        if (!response) {
            return
        }
        setInitValues(response)
        setToggle(true)
    }, [])

    function onSubmit(data) {
        console.log(data)
        dispatch(saveTable({...data, id_pensioner}))
    }
    const logoutCallback = () =>{
        dispatch(logout(username))
    }
    if  (!username) return <Redirect to={`${process.env.PUBLIC_URL}/login`} />
    if (!toogle) return <Preloader logout={logoutCallback} gtitle={'ביטוח סיכונים'} />
    return <>
        <Title>ביטוח סיכונים</Title>
        <Link onClick={logoutCallback}>Logout</Link>
        <Link px={5} as={NavLink} to={`${process.env.PUBLIC_URL}/pensionfunds`}>
            Pension Funds
        </Link>
        <Link px={10} as={NavLink} to={`${process.env.PUBLIC_URL}/dashboard`}>
            Dashboard
        </Link>
        <Link px={15} as={NavLink} to={`${process.env.PUBLIC_URL}/pensioner_data`}>
            Pensioner Data
        </Link>
        <Formik initialValues={initialValues} onSubmit={onSubmit} validate={tableValidator}>
            {formik => (
                <>
                    <Table>
                        {createItem('עדכון אחרון של מסך זה', 'last_upadete_of_this_screen', formik,
                        '#e8cb0c')}
                        {createItem('תקופה דוח','period_report', formik,'#e8cb0c')}
                        {createItem('שנת דוח ', 'report_year', formik,'#e8cb0c')}
                        {createItem('מועד פתיחת קופה', 'cash_opening_date', formik,'#e8cb0c')}
                        {createItem('שם קולקטיב', 'collective_name', formik,'#e8cb0c')}

                        {createItem('וג קופה', 'pos_type', formik,'#e8cb0c')}
                        {createItem('שם היצרן', 'manufacture_name', formik,'#e8cb0c')}
                    </Table>
                    <GTitle>הכיסויים הביטוחיים שלך בפוליסה</GTitle>
                    <Table>
                        {createItem('סכום ביטוח ', 'insuarunce_amount', formik,'#fca00a')}
                        {createItem('סך פרמיה שנתית', 'total_annual_premium', formik,'#fca00a')}
                        {createItem('כיסוי ביטוחי ', 'insurance_coverage', formik,'#fca00a')}
                        {createItem('שם מבוטח ', 'insurance_name', formik,'#fca00a')}
                    </Table>
                    <Table>
                        {createItem('דוא"ל ', 'agent_mail', formik,'white')}
                        {createItem('טלפון ', 'agent_post', formik,'white')}
                        {createItem('שם  סוכן', 'agent_name', formik,'white')}
                        {createItem('סוכנות ביטוח מטפלת', 'insurance_agency_handles', formik
                        ,'white')}
                    </Table>
                    <Table>
                        {createItem('הערות ', 'remarks', formik,'#d1d1d1')}
                    </Table>
                    <SaveButton onClick={formik.handleSubmit}/>
                </>
            )}
        </Formik>
    </>
}
export const SaveButton = ({onClick}) => <Button type={'primary'} style={{
    margin : '0 auto',
    width: '250px',
    height : '50px',
    fontSize: '22px',
    display : 'block',
    marginBottom : '30px'
}} onClick={onClick}>Save</Button>

export const Preloader = ({logout,gtitle}) => (
    <>
    <Link onClick={logout}>Logout</Link>
    <Title>{gtitle}</Title>
    <Card
    style={{ width: 1200, marginTop: 170 ,transform: 'scale(1.5)',margin: '0 auto'}}
>
    <Skeleton loading={true} avatar active>
    </Skeleton>
</Card>
<Card
    style={{ width: 1200, marginTop: 140 ,transform: 'scale(1.5)',margin: '0 auto'}}
>
    <Skeleton loading={true} avatar active>
    </Skeleton>
</Card></>)

const createItem = (title, value, options,color) => {
    const {handleChange, handleSubmit, values,errors} = options
    return <Item>
        <Header color={color}>{title}</Header>
        <Cell><Input onChange={handleChange}  value={values[value] || ''}
                     name={value} error={errors[value]}/></Cell>
    </Item>
}


export const Paginator = styled.div`
  display: flex;
  width: 300px;
  height: 65px;
  margin: 20px auto;
`
export const PagItem = styled.div`
  position: relative;
  display: block;
  padding: .5rem .100rem;
  margin-left: -1px;
  height: 100%;
  color: #276fb8;
  font-size: 25px;
  background-color: #fff;
  border: 1px solid #dee2e6;
  width: 100px;
  text-align: center;
  cursor: pointer;
`



export default Risk_insurances_table